import React, { Component } from 'react';

import { Link } from 'gatsby';

import { StaticImage } from 'gatsby-plugin-image';

//import logo from '../assets/images/bhavisha.png';
export default class SubHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openMenu: false,
    };
  }
  toggleMenu = (value) => {
    this.setState({ openMenu: value });
  };

  render() {
    const { openMenu } = this.state;

    return (
      <nav
        className="navbar navbar-expand-lg  navbar-light navbar-shrink fixed-top"
        id="mainNav"
      >
        <div className="container">
          <Link className="navbar-brand navbar-logo" to="/">
            {/*<img className="img-fluid" src={logo} alt="" />*/}
            <StaticImage
              src="../images/bhavisha.png"
              alt="Bhavisha Patel"
              className="img-fluid"
            />
          </Link>

          <button
            onClick={(_) => this.toggleMenu(!openMenu)}
            className={`navbar-toggler navbar-toggler-right ${
              openMenu ? '' : 'collapsed'
            }`}
            type="button"
            aria-controls="navbarResponsive"
            aria-expanded={openMenu}
            aria-label="Toggle navigation"
          >
            Menu
            <i className="fas fa-bars"></i>
          </button>
          <div
            className={`collapse navbar-collapse ${openMenu ? 'show' : ''}`}
            id="navbarResponsive"
          >
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link className="nav-link" to="/#projects">
                  PROJECTS
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/#visuals">
                  VISUALS
                </Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="/#about">
                  ABOUT
                </Link>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link"
                  href="https://www.bhavisha.ca/bhavisha-resume.pdf"
                  target="_blank"
                  rel="noreferrer"
                >
                  RESUME
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}
